
@import url("https://fonts.googleapis.com/css?family=Avenir:400,300");
.preloader{position:absolute;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;width:100%;height:100%
}
.dot-loader{width:6px;height:6px;border-radius:50%;animation:typing 1s linear infinite alternate;margin:46px auto;position:relative;left:-12px
}
@keyframes typing{
0%{background-color:#fff;box-shadow:12px 0px 0px 0px rgba(255,255,255,0.2),24px 0px 0px 0px rgba(255,255,255,0.2)
}
25%{background-color:rgba(255,255,255,0.4);box-shadow:12px 0px 0px 0px #fff,24px 0px 0px 0px rgba(255,255,255,0.2)
}
75%{background-color:rgba(255,255,255,0.4);box-shadow:12px 0px 0px 0px rgba(255,255,255,0.2),24px 0px 0px 0px #fff
}
}
.location_indicator{margin:30px auto;position:relative;left:-9px
}
.location_indicator:before,.location_indicator:after{position:absolute;content:""
}
.location_indicator:before{width:20px;height:20px;border-radius:100% 100% 100% 0;box-shadow:0px 0px 0px 2px #fff;animation:mapping 1s linear infinite;transform:rotate(-46deg)
}
.location_indicator:after{width:30px;height:10px;border-radius:100%;left:44px;background-color:rgba(255,255,255,0.2);top:24px;z-index:-1
}
@-keyframes mapping{
0%{top:0
}
50%{top:-5px
}
100%{top:0
}
}
.poi:after{content:'';position:absolute;z-index:1;width:100%;height:100%;top:0;left:0;border-radius:100%;background-color:transparent;animation:pulse 1s infinite
}
@keyframes pulse{
0%{transform:scale(1);box-shadow:inset 0 0 1px 1px rgba(255,207,15,0.8)
}
50%{box-shadow:inset 0 0 1px 1px rgba(255,207,15,0.8)
}
100%{transform:scale(1.6);box-shadow:inset 0 0 1px 1px rgba(255,207,15,0)
}
}
.heart:after{content:"\2764";color:#f45660
}
body{color:#526475;margin:0px;padding:0px;font-family:'Avenir';font-size:16px;font-weight:300;background-color:#fff
}
blockquote{border-left:4px solid #6a7e95;padding-left:20px;padding-right:5px;padding-bottom:5px;padding-top:5px
}
a{color:#09a0f6;font-weight:normal;text-decoration:none;transition:all 0.3s
}
a:hover{text-decoration:underline;cursor:pointer
}
p{line-height:28px;margin-bottom:20px;margin-top:0px;display:block
}
ul,ol{line-height:28px;margin-bottom:16px;margin-top:0px
}
ul li,ol li{line-height:28px
}
small{color:#6a7e95;font-size:14px
}
h1,h2,h3,h4,h5,h6{font-weight:300;color:#526475;display:block;margin-bottom:20px;margin-top:0px
}
h1{font-size:36px;line-height:50px
}
h2{font-size:32px;line-height:46px
}
h3{font-size:28px;line-height:42px
}
h4{font-size:24px;line-height:38px
}
h5{font-size:20px;line-height:34px
}
h6{font-size:16px;line-height:30px
}
.alert{display:block;width:calc(100% - 30px);font-weight:300;font-size:16px;text-align:left;padding-left:14px;padding-right:14px;padding-top:16px;padding-bottom:16px;margin-top:0px;margin-bottom:20px;border-radius:5px;border-width:1px;border-style:solid;background-color:#E1F5FE;color:#03A9F4;border-color:#03A9F4
}
.alert a{text-decoration:none;font-weight:normal
}
.alert-error{color:#D32F2F;background-color:#FFEBEE;border-color:#F44336
}
.alert-warning{background-color:#FFF8E1;color:#FF8F00;border-color:#FFC107
}
.alert-done{background-color:#E8F5E9;color:#388E3C;border-color:#4CAF50
}
.btn{font-family:'Avenir';font-size:16px;font-weight:300;-webkit-touch-callout:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;text-align:center;text-decoration:none !important;line-height:28px;margin-left:5px;margin-right:5px;margin-top:5px;margin-bottom:20px;padding-left:25px;padding-right:25px;padding-top:5px;padding-bottom:5px;display:inline-block;cursor:pointer;border-radius:5px;transition:all 0.3s;color:#fff;background-color:#09a0f6;border:0px
}
.btn:hover{text-decoration:none
}
.btn:focus{border:1px solid #09a0f6
}
.btn:active{border:1px solid #09a0f6
}
.btn:hover{opacity:0.8
}
.btn-small{font-size:14px !important;line-height:20px !important;padding-top:4px !important;padding-bottom:4px !important;padding-left:15px !important;padding-right:15px !important
}
.btn-big{font-size:22px !important;line-height:34px !important;padding-top:8px !important;padding-bottom:8px !important;padding-left:30px !important;padding-right:30px !important
}
.btn-outline{font-family:'Avenir';font-size:16px;font-weight:300;-webkit-touch-callout:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;text-align:center;text-decoration:none !important;line-height:28px;margin-left:5px;margin-right:5px;margin-top:5px;margin-bottom:20px;padding-left:25px;padding-right:25px;padding-top:5px;padding-bottom:5px;display:inline-block;cursor:pointer;border-radius:5px;transition:all 0.3s;color:#09a0f6;background-color:transparent;border:1px solid #09a0f6
}
.btn-outline:hover{text-decoration:none
}
.btn-outline:focus{border:1px solid #09a0f6
}
.btn-outline:active{border:1px solid #09a0f6
}
.btn-outline:hover{color:#fff;background-color:#09a0f6
}
code{font-family:'Avenir';font-size:16px;font-weight:300;border-radius:5px;background-color:#f1f5fa;color:#09a0f6
}
code{padding-left:6px;padding-right:6px
}
pre{font-family:'Avenir';font-size:16px;font-weight:300;display:block;width:calc(100% - 30px);padding:14px;line-height:28px
}
pre{margin-bottom:20px;border-radius:5px;background-color:#f1f5fa;color:#526475
}
pre{overflow-x:auto
}
.form-input[type="text"],.form-input[type="password"],.form-input[type="number"],.form-input[type="email"]{font-family:'Avenir';font-size:16px;font-weight:300;display:inline-block;width:100%;transition:all 0.3s;color:#526475;padding:10px;margin-top:0px;margin-bottom:20px;margin-left:5px;margin-right:5px;border:1px solid #d1e1e8;border-radius:5px;outline:0px;box-sizing:border-box;line-height:40px;height:40px
}
.form-input[type="text"]:focus,.form-input[type="password"]:focus,.form-input[type="number"]:focus,.form-input[type="email"]:focus{border:1px solid #09a0f6
}
.form-input[type="date"]{font-family:'Avenir';font-size:16px;font-weight:300;display:inline-block;width:100%;transition:all 0.3s;color:#526475;padding:10px;margin-top:0px;margin-bottom:20px;margin-left:5px;margin-right:5px;border:1px solid #d1e1e8;border-radius:5px;outline:0px;box-sizing:border-box;width:auto !important;height:40px
}
.form-input[type="date"]:focus{border:1px solid #09a0f6
}
.form-input[disabled]{font-family:'Avenir';font-size:16px;font-weight:300;display:inline-block;width:100%;transition:all 0.3s;color:#526475;padding:10px;margin-top:0px;margin-bottom:20px;margin-left:5px;margin-right:5px;border:1px solid #d1e1e8;border-radius:5px;outline:0px;box-sizing:border-box;cursor:not-allowed;background-color:#d1e1e8;height:40px
}
.form-input[disabled]:focus{border:1px solid #09a0f6
}
.form-input[type="submit"],.form-input[type="button"]{font-family:'Avenir';font-size:16px;font-weight:300;-webkit-touch-callout:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;text-align:center;text-decoration:none !important;line-height:28px;margin-left:5px;margin-right:5px;margin-top:5px;margin-bottom:20px;padding-left:25px;padding-right:25px;padding-top:5px;padding-bottom:5px;display:inline-block;cursor:pointer;border-radius:5px;transition:all 0.3s;color:#fff;background-color:#09a0f6;border:0px
}
.form-input[type="submit"]:hover,.form-input[type="button"]:hover{text-decoration:none
}
.form-input[type="submit"]:focus,.form-input[type="button"]:focus{border:1px solid #09a0f6
}
.form-input[type="submit"]:active,.form-input[type="button"]:active{border:1px solid #09a0f6
}
.form-input[type="submit"]:hover,.form-input[type="button"]:hover{opacity:0.8
}
.form-select{font-family:'Avenir';font-size:16px;font-weight:300;display:inline-block;width:100%;transition:all 0.3s;color:#526475;padding:10px;margin-top:0px;margin-bottom:20px;margin-left:5px;margin-right:5px;border:1px solid #d1e1e8;border-radius:5px;outline:0px;box-sizing:border-box;padding-top:6px;height:40px;background-color:#fff
}
.form-select:focus{border:1px solid #09a0f6
}
.form-textarea{font-family:'Avenir';font-size:16px;font-weight:300;display:inline-block;width:100%;transition:all 0.3s;color:#526475;padding:10px;margin-top:0px;margin-bottom:20px;margin-left:5px;margin-right:5px;border:1px solid #d1e1e8;border-radius:5px;outline:0px;box-sizing:border-box;resize:vertical
}
.form-textarea:focus{border:1px solid #09a0f6
}
.form-auto{width:auto !important
}
