body{padding:0;margin:0
}
label{display:block
}
input[type="range"]{height:40px;width:100%
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100vh;width:100vw;position:absolute;top:0;left:0;z-index:0;overflow:hidden
}
.map-wrapper{height:100%;width:100%;z-index:1
}
.map-wrapper #map{height:100%;width:100%
}
.controls-wrapper{position:absolute;width:80%;z-index:10;left:0;top:0;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;transition:all .25s ease
}
.controls-wrapper:hover{background:rgba(255,255,255,0.75)
}
.controls-wrapper .navbar .form-input,.controls-wrapper .navbar .btn{margin:0
}
.sidebar-wrapper{background:rgba(255,255,255,0.75);position:absolute;width:20%;height:100%;z-index:5;top:0;right:0
}
#forecast-wrapper{display:block;height:100%;padding:1rem;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:justify;justify-content:space-between
}
#forecast-wrapper .weather-situation,#forecast-wrapper .wind-situation{text-align:center;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
#forecast-wrapper .weather-situation i,#forecast-wrapper .wind-situation i{font-size:5rem;width:100px
}
#forecast-wrapper .weather-situation span,#forecast-wrapper .wind-situation span{font-size:3rem;padding-left:2rem;padding-top:1rem
}
#forecast-wrapper .weather-situation .wrapper,#forecast-wrapper .wind-situation .wrapper{width:100px;height:100px;overflow:hidden;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
#forecast-wrapper table{text-align:left
}
#forecast-wrapper table i{width:1.5rem
}



